@use "../globals/theme";


// Table
// --------------------------------------------------

$cell-padding:          .75rem !default;

$bg:                    transparent !default;
$accent-bg:             rgba(0, 0, 0, .05) !default;
$hover-bg:              rgba(0, 0, 0, .075) !default;
$active-bg:             $hover-bg !default;

$border-width:          1px !default;
$border-color:          theme.$background-color-step-300 !default;

$head-bg:               theme.$background-color-step-200 !default;
$head-color:            theme.$background-color-step-700 !default;

$striped-order:         odd !default;
